.chat {
	display: flex;
	align-items: center;
	justify-content: space-evenly;
	padding: 20px;
	height: 70px;
	border-bottom: 1px solid pink;
}

a {
	text-decoration: none;
	color: inherit;
}

.chat_details{
	flex: 1;
}

.chat_details > p{
	color: gray;
}

.chat_img{
	margin-right: 20px;
}

.chat_timestamp{
	color: lightgray;
}