.card {
		position: relative;
		width: 600px;
		padding: 20px;
		max-width: 85vw;
		height: 50vh;
		border-radius: 20px;
		background-position: center;
		background-size: cover;
		box-shadow: 0px 10px 33px 0px rgba(0,0,0,0.3);
}

.card > h3 {
		position: absolute;
		bottom: 10px;
		text-shadow: 1px 1px 5px #000000;
		color: white;
}
.tinderCards_container {
		display: flex;
		justify-content: center;
		margin-top: 5vh;
}

.swipe {
		position: absolute;
}